<template>
	<div id="app">
		<Temperature v-bind:temperature="weather.items[0].general.temperature" />
		<div class="row">
			<div class="col-1"></div>
			<div class="general col-5">
				<General v-bind:generalForecast="weather.items[0].general.forecast" v-bind:iconNames="iconNames" />
			</div>
			<div class="region col-5">
				<Region v-bind:regionsForecast="weather.items[0].periods[0].regions" v-bind:iconNames="iconNames" />
			</div>
			<div class="col-1"></div>
		</div>
	</div>
</template>

<script>
import { DateTime } from 'luxon';
import Temperature from './components/Temperature';
import General from './components/General';
import Region from './components/Region';

export default {
	name: 'app',
	components: {
		Temperature,
		General,
		Region
	},
	data() {
		return {
			weather: [],
			iconNames: ['fas fa-bolt', 'fas fa-cloud-showers-heavy', 'fas fa-cloud-sun-rain', 'fas fa-cloud-sun', 'fas fa-cloud-moon', 'fas fa-cloud', 'fas fa-wind']
		}
	},
	created() {
		var now = DateTime.local();
		//console.log(now.toString());
		var date = now.toISO().substring(0, 19);
		console.log(date);
		var url = 'https://api.data.gov.sg/v1/environment/24-hour-weather-forecast?date_time=' + date;
		//console.log(url);
		fetch(url)
			.then(response => response.json())
			.then(data => this.weather = data)
			.catch(error => console.error(error))
	}
}
</script>

